import {stopSubmit} from "redux-form";
import {questionAPI} from "../../api/api";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";

const swal = withReactContent(Swal);

const SET_QUESTIONS = '/build-the-world/question/SET_QUESTIONS';
const SET_QUESTION_INFO = '/build-the-world/question/SET_QUESTION_INFO';
const CHANGE_QUESTION = '/build-the-world/question/CHANGE_QUESTION';
const SET_ANSWER = '/build-the-world/question/SET_ANSWER';
const SET_ASSETS = '/build-the-world/question/SET_ASSETS';

const initialState = {
    questions: null,
    currQuestion: null,
    answers: [],
    questionAssets: {
        data: []
    }
};

const questionReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_QUESTIONS: {
            return {
                ...state,
                questions: action.payload
            };
        }
        case SET_QUESTION_INFO: {
            return {
                ...state,
                currQuestion: action.payload.question,
                answers: action.payload.answers,
                questionAssets: action.payload.assets
            };
        }
        case CHANGE_QUESTION: {
            return {
                ...state,
                currQuestion: action.payload
            };
        }
        case SET_ANSWER: {
            return {
                ...state,
                answers: action.payload
            };
        }
        case SET_ASSETS: {
            return {
                ...state,
                questionAssets: action.payload
            };
        }
        default: {
            return state;
        }
    }
};

export const setQuestions = (questions) => ({type: SET_QUESTIONS, payload: questions});
export const setQuestionInfo = (question, answers, assets) => ({type: SET_QUESTION_INFO, payload: {question, answers, assets}});
export const setQuestionText = (question) => ({type: CHANGE_QUESTION, payload: question});
export const setAnswer = (answer) => ({type: SET_ANSWER, payload: answer});
export const setAssets = (assets) => ({type: SET_ASSETS, payload: assets});

export const getQuestions = () => async (dispatch) => {
    const response = await questionAPI.getquestions();
    if (response.data.resultCode === 1) {
        dispatch(setQuestions(response.data.data));
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export const getQuestionInfo = (questionId) => async (dispatch) => {
    const response = await questionAPI.getquestioninfo(questionId);
    if (response.data.resultCode === 1) {
        const {question, answers, assets} = response.data.data;
        dispatch(setQuestionInfo(question, answers, assets));
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export const changeAnswer = (answers) => async (dispatch) => {
    dispatch(setAnswer(answers));
}

export const changeQuestion = (question) => async (dispatch) => {
    dispatch(setQuestionText(question));
}

export const getNewAssets = (question, type) => async (dispatch) => {
    const response = await questionAPI.getassets(question, type);
    if (response.data.resultCode === 1) {
        dispatch(setAssets(response.data.data));
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export const changeAssets = (asset, question, type) => async (dispatch) => {
    const response = await questionAPI.deleteasset(asset, question);
    if (response.data.resultCode === 1) {
        dispatch(getNewAssets(question, type));
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export const saveQuestion = (question, answers, notifySuccessProgress) => async (dispatch) => {
    const response = await questionAPI.savequestion(question, answers);
    if (response.data.resultCode === 1) {
        await dispatch(getQuestions());
        notifySuccessProgress();
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export const uploadassets = (file, fileInfo) => async (dispatch) => {
    const formData = new FormData();
    formData.append("asset", file);
    formData.append("fileInfo", JSON.stringify(fileInfo));
    const response = await questionAPI.uploadfiles(formData);
    if (response.data.resultCode === 1) {
        await swal.fire({
            title: "Успех!",
            text: "Файлы были загружены.!",
            icon: "success",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(getNewAssets(fileInfo.question, fileInfo.type));
        });
    } else {
        dispatch(stopSubmit("getallquestions", {_error: response.data.message}));
    }
}

export default questionReducer;