import {profileAPI} from "../../api/api";
import {stopSubmit} from "redux-form";
import {RussianName} from "../../utils/name";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";

const swal = withReactContent(Swal);

const SET_USER_PROFILE = '/build-the-world/profile/SET_USER_PROFILE';

const initialState = {
    profile: {
        name: null,
        phone: null,
        social: null,
        school: null,
        classRoom: null,
        city: null,
        teacher: null,
        teacherPosition: null,
        email: null,
        regDate: null,
        endDate: null,
        percent: null
    },
    sex: null,
    testResult: {
        answers: [],
        percent: null
    }
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_PROFILE: {
            return {
                ...state,
                profile: {...action.payload},
                sex: action.sex
            };
        }
        default: {
            return state;
        }
    }
};

export const setUserProfile = (name, phone, social, school, classRoom, city, teacher, teacherPosition, email, regDate, endDate, percent, sex) => ({
    type: SET_USER_PROFILE,
    payload: {
        name,
        phone,
        social,
        school,
        classRoom,
        city,
        teacher,
        teacherPosition,
        email,
        regDate,
        endDate,
        percent
    },
    sex
});

export const getUserProfile = (userId) => async (dispatch) => {
    const response = await profileAPI.getProfile(userId);
    if (response.data.resultCode) {
        const {
            user_name, user_phone,
            user_social, user_school,
            user_class, user_city,
            user_teacher, user_teacherPosition,
            user_email, user_reg_date, user_endtest_date,
            user_percent
        } = response.data.data;
        const sex = new RussianName(user_name);
        dispatch(setUserProfile(
            user_name, user_phone,
            user_social, user_school,
            user_class, user_city,
            user_teacher, user_teacherPosition,
            user_email, user_reg_date, user_endtest_date,
            user_percent, sex.sex
        ));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getProfile", {_error: message}));
    }
}

export const setUserEmail = (userId, email) => async (dispatch) => {
    const response = await profileAPI.setEmail(userId, email);
    if (response.data.resultCode === 1) {
        await swal.fire({
            title: "Успех!",
            text: "Вы привязали свою почту к аккаунту!",
            icon: "success",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(getUserProfile(userId));
        });
    } else {
        const message = response.data.message;
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(stopSubmit("setEmail", {_error: message}));
        });
    }
}

export const setEndTest = (answers, userId) => async (dispatch) => {
    const response = await profileAPI.setendtest(answers, userId);
    const message = response.data.message;
    if (response.data.resultCode === 1) {
        dispatch(getUserProfile(userId));
        await swal.fire({
            title: "Конец!",
            text: 'Нажмите продолжить чтобы просмотреть результат',
            icon: "success",
            confirmButtonText: "Продолжить"
        });
    } else {
        await swal.fire({
            title: "Ошибка!",
            text: message,
            icon: "error",
            confirmButtonText: "OK"
        }).then(() => {
            dispatch(stopSubmit("setEmail", {_error: message}));
        });
    }
}

export default profileReducer;