import {usersAPI} from "../../api/api";
import {stopSubmit} from "redux-form";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";
import {RussianName} from "../../utils/name";

const swal = withReactContent(Swal);

const SET_USERS = '/build-the-world/users/SET_USERS';
const SET_CURRENT_USERS = '/build-the-world/users/SET_CURRENT_USERS';
const SET_COMMENT = '/build-the-world/users/SET_COMMENT';
const SET_REWARDS = '/build-the-world/users/SET_REWARDS';

const initialState = {
    users: [],
    cnt: 0,
    limitTo: 0,
    currentUser: null,
    reviews: null,
    creativeComment: null,
    rewards: null
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USERS: {
            return {
                ...state,
                users: [...action.payload.users],
                cnt: action.payload.cnt,
                limitTo: action.payload.limitTo
            };
        }
        case SET_CURRENT_USERS: {
            return {
                ...state,
                currentUser: action.payload.user,
                reviews: action.payload.reviews,
                creativeComment: action.payload.comment
            };
        }
        case SET_COMMENT: {
            return {
                ...state,
                creativeComment: action.comment
            };
        }
        case SET_REWARDS: {
            return {
                ...state,
                rewards: action.rewards
            };
        }
        default: {
            return state;
        }
    }
};

export const setUsers = (users, cnt, limitTo) => ({
    type: SET_USERS,
    payload: {users, cnt, limitTo}
});

export const setCurrentUser = (user, reviews, comment) => ({
    type: SET_CURRENT_USERS,
    payload: {user, reviews, comment}
});

export const setComment = (comment) => ({
    type: SET_COMMENT,
    comment
});

export const setRewards = (rewards) => ({type: SET_REWARDS, rewards});

export const getUsers = (filter) => async (dispatch) => {
    const response = await usersAPI.getusers(filter);
    if (response.data.resultCode === 1) {
        dispatch(setUsers(response.data.data, response.data.dataCnt, response.data.limitTo));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getUsers", {_error: message}));
    }
}

export const deleteUser = (userId, userLogin, onSubmit) => (dispatch) => {
    swal.fire({
        title: "Внимание!",
        text: `Вы действительно хотите удалить пользователя с ID ${userId} ?`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: 'Да, удалить!',
        cancelButtonText: 'Отмена',
        customClass: {
            confirmButton: 'btn btn-outline-danger',
            cancelButton: 'btn btn-primary ml-1'
        },
        buttonsStyling: false
    }).then(async function (result) {
        if (result.value) {
            const response = await usersAPI.deleteuser(userId, userLogin);
            if (response.data.resultCode === 1) {
                await swal.fire({
                    icon: 'success',
                    title: 'Успешно!',
                    text: 'Пользователь был удален',
                    customClass: {
                        confirmButton: 'btn btn-success'
                    }
                }).then(function () {
                    onSubmit();
                });
            } else {
                const message = response.data.message;
                dispatch(stopSubmit("deleteUser", {_error: message}));
            }
        }
    })
}

export const getUserById = (userId) => async (dispatch) => {
    const response = await usersAPI.getuser(userId);
    if (response.data.resultCode === 1) {
        const {user_name} = response.data.data;
        const sex = new RussianName(user_name).sex;
        response.data.data = {...response.data.data, sex}
        dispatch(setCurrentUser(response.data.data, response.data.reviews, response.data.comment));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getUserById", {_error: message}));
    }
}

export const sendCreativeComment = (userId, comment, isComment) => async (dispatch) => {
    const response = await usersAPI.sendcomment(userId, comment, isComment);
    if (response.data.resultCode === 1) {
        dispatch(setComment(comment));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getUserById", {_error: message}));
    }
}

export const getRewards = (userLogin) => async (dispatch) => {
    const response = await usersAPI.getrewards(userLogin);
    if (response.data.resultCode === 1) {
        dispatch(setRewards(response.data.assets));
    } else {
        dispatch(stopSubmit("getRewards", {_error: 'error'}));
    }
}

export const resetTesting = (userId, userLogin) => (dispatch) => {
    swal.fire({
        title: "Внимание!",
        text: `Вы действительно хотите сбросить тестирование для пользователя ${userLogin} ?`,
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: 'Да, сбросить!',
        cancelButtonText: 'Отмена',
        customClass: {
            confirmButton: 'btn btn-outline-danger',
            cancelButton: 'btn btn-primary ml-1'
        },
        buttonsStyling: false
    }).then(async function (result) {
        if (result.value) {
            const response = await usersAPI.resettest(userId, userLogin);
            if (response.data.resultCode === 1) {
                dispatch(getUserById(userId));
                dispatch(getRewards(userLogin));
            } else {
                const message = response.data.message;
                dispatch(stopSubmit("resetTesting", {_error: message}));
            }
        }
    })
}

export default usersReducer;