import {statAPI} from "../../api/api";
import {stopSubmit} from "redux-form";


const SET_REGISTRATIONS = '/build-the-world/stat/SET_REGISTRATIONS';
const SET_TEST = '/build-the-world/stat/SET_TEST';
const SET_STAT = '/build-the-world/stat/SET_STAT';

const initialState = {
    registrationOptions: {
        categories: [],
        data: []
    },
    testOptions: {
        categories: [],
        data: []
    },
    selectedDates: {
        reg: {},
        test: {}
    },
    allStat: null
};

const statReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_REGISTRATIONS:
            return {
                ...state,
                registrationOptions: {...state.registrationOptions, categories: [...action.payload.categories], data: [...action.payload.data]},
                selectedDates: {...state.selectedDates, reg: {...action.payload.selectedDates}}
            }
        case SET_TEST:
            return {
                ...state,
                testOptions: {...state.testOptions, categories: [...action.payload.categories], data: [...action.payload.data]},
                selectedDates: {...state.selectedDates, test: {...action.payload.selectedDates}}
            }
        case SET_STAT:
            return {
                ...state,
                allStat: {...action.stat}
            }
        default: {
            return state;
        }
    }
}

const setChart = (type, categories, data, selectedDates) => ({type, payload: {categories, data, selectedDates}});
const setStat = (stat) => ({type: SET_STAT, stat});

export const getChart = (dates, tableName) => async (dispatch) => {
    const response = await statAPI.getchartbydate(dates, tableName);
    if (response.data.resultCode === 1) {
        switch (tableName) {
            case 'user_reg_date':
                return dispatch(setChart(SET_REGISTRATIONS, response.data.categories, response.data.data, response.data.selectedDates));
            case 'user_endtest_date':
                return dispatch(setChart(SET_TEST, response.data.categories, response.data.data, response.data.selectedDates));
            default: {
                return tableName;
            }
        }
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getchart", {_error: message}));
    }
}

export const getAllStat = () => async (dispatch) => {
    const response = await statAPI.getallstat();
    if (response.data.resultCode === 1) {
        dispatch(setStat(response.data.stat));
    } else {
        const message = response.data.message;
        dispatch(stopSubmit("getchart", {_error: message}));
    }
}

export const createExcel = () => async (dispatch) => {
    const response = await statAPI.createexcel();
    if (response.data.resultCode === 1) {

    } else {
        const message = response.data.message;
        dispatch(stopSubmit("createExcel", {_error: message}));
    }
}

export default statReducer;