import * as axios from "axios"
import configApi from './config'

const instance = axios.create({
    baseURL: configApi.SERVER_URI,
    withCredentials: true
});

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`/profile/getprofile/${userId}/`);
    },
    setEmail(userId, email) {
        return instance.post(`/profile/setemail/`, {userId, email});
    },
    setendtest(answers, userId) {
        return instance.post(`/profile/setendtest/`, {answers, userId});
    }
}

export const statAPI = {
    getchartbydate(dates, tableName) {
        return instance.post(`/stat/getchartbydate/`, {dates, tableName});
    },
    getallstat() {
        return instance.post(`/stat/getallstat/`);
    },
    createexcel() {
        return instance.get(`/stat/createexcel/`);
    }
}

export const usersAPI = {
    getusers(filter) {
        return instance.post(`/users/getusers/`, filter);
    },
    deleteuser(userId, userLogin) {
        return instance.post(`/users/deleteuser/`, {userId, userLogin});
    },
    getuser(userId) {
        return instance.post(`/users/getuser/`, userId);
    },
    resettest(userId, userLogin) {
        return instance.post(`/users/resettest/`, {userId, userLogin});
    },
    sendcomment(userId, comment, isComment) {
        return instance.post(`/users/sendcomment/`, {userId, comment, isComment});
    },
    getrewards(userLogin) {
        return instance.post(`/profile/getrewards/`, {userLogin});
    }
}

export const authAPI = {
    me() {
        return instance.get(`/auth/meadm/`);
    },
    login(userObject) {
        return instance.post(`/auth/signinadm/`, userObject);
    },
    loginecoworld(userObject) {
        return instance.post(`/auth/signinecoworld/`, userObject);
    },
    setEcoId(userId) {
        return instance.post(`/auth/bindecoworld/`, userId);
    },
    register(userObject) {
        return instance.post(`/auth/signup/`, userObject);
    },
    logout() {
        return instance.get(`/auth/logoutadm/`);
    },
    changePassword(userId, login, currPassword, newPassword) {
        return instance.post(`/auth/changepassword/`, {userId, login, currPassword, newPassword});
    }
}

export const questionAPI = {
    getquestions() {
        return instance.get(`/question/getquestions/`);
    },
    getquestioninfo(questionId) {
        return instance.post(`/question/getquestioninfo/`, questionId);
    },
    getassets(question, type) {
        return instance.post('/question/getassets/', {question, type});
    },
    deleteasset(asset, question) {
        return instance.post('/question/deleteasset/', {asset, question});
    },
    savequestion(question, answers) {
        return instance.post(`/question/savequestion/`, {question, answers});
    },
    uploadfiles(file) {
        return instance.post('/question/uploadassets/', file, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        });
    }
}
